// src/app/admin/courses/page.tsx
"use client";

import { useEffect, useMemo, useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import {
  Plus, Search, Filter, Pencil, Copy, Trash2, X, Save,
  ChevronUp, ChevronDown, Check, Loader2, ToggleLeft, ToggleRight
} from "lucide-react";
import { supabaseBrowser } from "@/lib/supabase/client";
import { useToast } from "@/components/Toast";

/* ================== Tipos ================== */
type Course = {
  id: string;
  title: string;
  description: string | null;
  is_active: boolean;
  updated_at: string;
  modules?: { id: string }[]; // usado só pra contar rápido
};

type ModuleRow = {
  id: string;
  course_id: string;
  title: string;
  sort_order: number;
  is_active: boolean;
  updated_at: string | null;
};

/* ================== Página ================== */
export default function AdminCourses() {
  const sb = useMemo(() => supabaseBrowser(), []);
  const toast = useToast();

  const [q, setQ] = useState("");
  const [loading, setLoading] = useState(true);
  const [rows, setRows] = useState<Course[]>([]);
  const [page, setPage] = useState(1);
  const pageSize = 10;

  const [drawerOpen, setDrawerOpen] = useState(false);
  const [editingId, setEditingId] = useState<string | null>(null);

  /* ------- carregar ------- */
  async function load() {
    setLoading(true);
    // pega os cursos + array de modules (para contar)
    const { data, error } = await sb
      .from("courses")
      .select("id,title,description,is_active,updated_at, modules(id)")
      .order("updated_at", { ascending: false });

    if (error) {
      console.error(error);
      toast.push("Falha ao carregar cursos.");
      setLoading(false);
      return;
    }
    setRows((data as Course[]) ?? []);
    setLoading(false);
  }

  useEffect(() => { load(); /* eslint-disable-next-line */ }, []);

  /* ------- ações rápidas ------- */
  async function createCourse() {
    const title = prompt("Título do novo curso:");
    if (!title) return;
    const { data, error } = await sb
      .from("courses")
      .insert({ title, is_active: true })
      .select("*")
      .single();
    if (error) {
      toast.push("Não foi possível criar o curso.");
      return;
    }
    toast.push("Curso criado.");
    setRows((s) => [data as Course, ...s]);
    setEditingId((data as Course).id);
    setDrawerOpen(true);
  }

  async function duplicateCourse(c: Course) {
    const { data: newCourse, error } = await sb
      .from("courses")
      .insert({
        title: `${c.title} (cópia)`,
        description: c.description,
        is_active: false,
      })
      .select("*")
      .single();

    if (error || !newCourse) {
      toast.push("Falha ao duplicar curso.");
      return;
    }

    // clonar módulos
    const { data: oldMods } = await sb
      .from("modules")
      .select("*")
      .eq("course_id", c.id)
      .order("sort_order", { ascending: true });

    if (Array.isArray(oldMods) && oldMods.length) {
      const cloned = oldMods.map((m: ModuleRow) => ({
        course_id: (newCourse as any).id,
        title: m.title,
        sort_order: m.sort_order,
        is_active: m.is_active,
      }));
      await sb.from("modules").insert(cloned);
    }

    toast.push("Curso duplicado.");
    await load();
  }

  async function deleteCourse(c: Course) {
    const ok = confirm(`Excluir o curso "${c.title}"?\nIsto removerá também seus módulos.`);
    if (!ok) return;
    // primeiro apaga módulos
    await sb.from("modules").delete().eq("course_id", c.id);
    const { error } = await sb.from("courses").delete().eq("id", c.id);
    if (error) {
      toast.push("Falha ao excluir.");
      return;
    }
    toast.push("Curso excluído.");
    setRows((s) => s.filter((r) => r.id !== c.id));
  }

  async function toggleActive(c: Course) {
    const { error, data } = await sb
      .from("courses")
      .update({ is_active: !c.is_active })
      .eq("id", c.id)
      .select("*")
      .single();
    if (error) {
      toast.push("Falha ao alterar status.");
      return;
    }
    setRows((s) => s.map((r) => (r.id === c.id ? (data as Course) : r)));
  }

  /* ------- paginação ------- */
  const filtered = rows.filter((r) =>
    r.title.toLowerCase().includes(q.toLowerCase().trim())
  );
  const totalPages = Math.max(1, Math.ceil(filtered.length / pageSize));
  const pageRows = filtered.slice((page - 1) * pageSize, page * pageSize);

  /* ------- UI ------- */
  return (
    <motion.main
      initial={{ opacity: 0, y: 8 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.35 }}
      className="px-5 md:px-8 py-8 text-white"
    >
      <div className="max-w-6xl mx-auto">
        <div className="relative overflow-hidden rounded-3xl p-6 md:p-7
                        bg-gradient-to-b from-neutral-900/95 to-black/90
                        backdrop-blur-md border border-white/10
                        shadow-[inset_0_0_0_1px_rgba(255,255,255,0.05)]">
          {/* aurora */}
          <motion.div
            className="pointer-events-none absolute -top-24 left-1/2 -translate-x-1/2 h-64 w-64 rounded-full blur-3xl"
            style={{ background: "radial-gradient(circle, rgba(163,230,53,0.22), transparent)" }}
            animate={{ opacity: [0.25, 0.4, 0.25], scale: [0.95, 1.05, 0.95] }}
            transition={{ duration: 6, repeat: Infinity }}
          />

          {/* header */}
          <div className="relative z-10 flex items-center justify-between gap-3">
            <div>
              <h1 className="text-xl md:text-2xl font-semibold text-lime-300 drop-shadow-[0_0_10px_rgba(163,230,53,0.25)]">
                Cursos
              </h1>
              <p className="text-sm text-white/70">Gerencie cursos e módulos.</p>
            </div>
            <button
              onClick={createCourse}
              className="h-10 px-3 rounded-xl bg-lime-400/10 text-lime-300 hover:bg-lime-400/20
                         hover:shadow-[0_0_10px_rgba(163,230,53,0.25)] transition inline-flex items-center gap-2"
            >
              <Plus size={16} /> Adicionar curso
            </button>
          </div>

          {/* toolbar */}
          <div className="relative z-10 mt-6 flex flex-col sm:flex-row gap-3">
            <div className="flex-1 relative">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 opacity-60" size={16} />
              <input
                value={q}
                onChange={(e) => { setQ(e.target.value); setPage(1); }}
                placeholder="Buscar curso…"
                className="w-full h-11 pl-9 pr-3 rounded-xl bg-white/5 border border-white/10
                           outline-none focus:ring-2 focus:ring-lime-400/25"
              />
            </div>
            <button
              className="h-11 px-3 rounded-xl bg-white/5 border border-white/10 inline-flex items-center gap-2 hover:bg-white/10 transition"
              type="button"
            >
              <Filter size={16} /> Filtros
            </button>
          </div>

          {/* tabela */}
          <div className="relative z-10 mt-6 rounded-2xl overflow-hidden bg-white/[0.03] border border-white/10">
            <div className="hidden md:grid grid-cols-[1fr,120px,140px,160px,140px] px-6 py-3 text-xs uppercase tracking-wide text-white/60 border-b border-white/10">
              <div>Curso</div>
              <div>Módulos</div>
              <div>Status</div>
              <div>Atualizado</div>
              <div>Ações</div>
            </div>

            {loading ? (
              <ul className="divide-y divide-white/10 animate-pulse">
                {Array.from({ length: 6 }).map((_, i) => (
                  <li key={i} className="grid md:grid-cols-[1fr,120px,140px,160px,140px] grid-cols-1 gap-3 px-6 py-5">
                    <div className="h-4 w-64 bg-white/10 rounded" />
                    <div className="h-4 w-10 bg-white/10 rounded" />
                    <div className="h-4 w-20 bg-white/10 rounded" />
                    <div className="h-4 w-24 bg-white/10 rounded" />
                    <div className="h-8 w-28 bg-white/10 rounded" />
                  </li>
                ))}
              </ul>
            ) : filtered.length === 0 ? (
              <div className="px-6 py-12 text-center">
                <div className="text-white/70">Nenhum curso encontrado.</div>
                <button
                  onClick={createCourse}
                  className="mt-3 h-10 px-3 rounded-xl bg-lime-400/10 text-lime-300 hover:bg-lime-400/20 transition inline-flex items-center gap-2"
                >
                  <Plus size={16} /> Criar curso
                </button>
              </div>
            ) : (
              <ul className="divide-y divide-white/10">
                {pageRows.map((r) => {
                  const modsCount = r.modules?.length ?? 0;
                  return (
                    <li
                      key={r.id}
                      className="grid md:grid-cols-[1fr,120px,140px,160px,140px] grid-cols-1 gap-3 px-6 py-5 hover:bg-white/[0.04] transition-colors"
                    >
                      <div className="cursor-pointer" onDoubleClick={() => { setEditingId(r.id); setDrawerOpen(true); }}>
                        <div className="font-medium text-white/90">{r.title}</div>
                        {r.description && <div className="text-sm text-white/60 line-clamp-2">{r.description}</div>}
                      </div>
                      <div className="text-sm text-white/80 self-center">{modsCount}</div>
                      <div className="self-center">
                        <button
                          onClick={() => toggleActive(r)}
                          className="inline-flex items-center gap-2 text-[11px] font-semibold px-2 py-1 rounded-md border transition
                                     hover:scale-[1.02]"
                          title="Alternar ativo"
                        >
                          {r.is_active ? (
                            <>
                              <ToggleRight size={14} className="text-lime-300" />
                              <span className="text-lime-300">Ativo</span>
                            </>
                          ) : (
                            <>
                              <ToggleLeft size={14} className="text-white/60" />
                              <span className="text-white/60">Inativo</span>
                            </>
                          )}
                        </button>
                      </div>
                      <div className="text-sm text-white/70 self-center">
                        {new Date(r.updated_at).toLocaleDateString("pt-BR", {
                          day: "2-digit", month: "short", year: "numeric",
                        }).replace(".", "")}
                      </div>
                      <div className="self-center flex items-center gap-2">
                        <button
                          className="h-8 w-8 grid place-items-center rounded-lg bg-white/5 border border-white/10 hover:bg-white/10"
                          title="Editar"
                          onClick={() => { setEditingId(r.id); setDrawerOpen(true); }}
                        >
                          <Pencil size={16} />
                        </button>
                        <button
                          className="h-8 w-8 grid place-items-center rounded-lg bg-white/5 border border-white/10 hover:bg-white/10"
                          title="Duplicar"
                          onClick={() => duplicateCourse(r)}
                        >
                          <Copy size={16} />
                        </button>
                        <button
                          className="h-8 w-8 grid place-items-center rounded-lg bg-red-500/15 border border-red-400/30 text-red-300 hover:bg-red-500/25"
                          title="Excluir"
                          onClick={() => deleteCourse(r)}
                        >
                          <Trash2 size={16} />
                        </button>
                      </div>
                    </li>
                  );
                })}
              </ul>
            )}
          </div>

          {/* paginação */}
          {filtered.length > 0 && (
            <div className="relative z-10 mt-4 flex items-center justify-end gap-2 text-sm">
              <button
                onClick={() => setPage((p) => Math.max(1, p - 1))}
                disabled={page <= 1}
                className="h-9 px-3 rounded-xl bg-white/5 border border-white/10 hover:bg-white/10 disabled:opacity-40"
              >
                Anterior
              </button>
              <span className="text-white/60">
                {page} / {totalPages}
              </span>
              <button
                onClick={() => setPage((p) => Math.min(totalPages, p + 1))}
                disabled={page >= totalPages}
                className="h-9 px-3 rounded-xl bg-white/5 border border-white/10 hover:bg-white/10 disabled:opacity-40"
              >
                Próxima
              </button>
            </div>
          )}
        </div>
      </div>

      <CourseDrawer
        open={drawerOpen}
        courseId={editingId}
        onClose={() => setDrawerOpen(false)}
        onSaved={() => { setDrawerOpen(false); load(); }}
      />
    </motion.main>
  );
}

/* ================== Drawer de Edição ================== */
function CourseDrawer({
  open,
  courseId,
  onClose,
  onSaved,
}: {
  open: boolean;
  courseId: string | null;
  onClose: () => void;
  onSaved: () => void;
}) {
  const sb = useMemo(() => supabaseBrowser(), []);
  const toast = useToast();

  const [tab, setTab] = useState<"general" | "modules">("general");

  // course
  const [loading, setLoading] = useState(false);
  const [saving, setSaving] = useState(false);
  const [title, setTitle] = useState("");
  const [desc, setDesc] = useState<string | null>("");
  const [active, setActive] = useState(true);

  // modules
  const [mods, setMods] = useState<ModuleRow[]>([]);
  const [modsLoading, setModsLoading] = useState(false);

  useEffect(() => {
    if (!open || !courseId) return;
    setTab("general");
    (async () => {
      setLoading(true);
      const { data, error } = await sb
        .from("courses")
        .select("*")
        .eq("id", courseId)
        .single();
      if (error) {
        toast.push("Falha ao carregar o curso.");
        setLoading(false);
        return;
      }
      setTitle((data as any).title || "");
      setDesc((data as any).description ?? "");
      setActive(!!(data as any).is_active);
      setLoading(false);
    })();

    (async () => {
      setModsLoading(true);
      const { data } = await sb
        .from("modules")
        .select("*")
        .eq("course_id", courseId)
        .order("sort_order", { ascending: true });
      setMods((data as any as ModuleRow[]) ?? []);
      setModsLoading(false);
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [open, courseId]);

  async function saveGeneral() {
    if (!courseId) return;
    if (!title.trim()) {
      toast.push("Informe um título.");
      return;
    }
    setSaving(true);
    const { error } = await sb
      .from("courses")
      .update({
        title: title.trim(),
        description: desc ?? null,
        is_active: active,
        updated_at: new Date().toISOString(),
      })
      .eq("id", courseId);
    setSaving(false);
    if (error) {
      toast.push("Falha ao salvar.");
      return;
    }
    toast.push("Salvo ✓");
    onSaved();
  }

  async function addModule() {
    if (!courseId) return;
    const title = prompt("Título do módulo:");
    if (!title) return;

    const nextOrder = (mods.at(-1)?.sort_order ?? 0) + 1;
    const { data, error } = await sb
      .from("modules")
      .insert({
        course_id: courseId,
        title,
        sort_order: nextOrder,
        is_active: true,
      })
      .select("*")
      .single();

    if (error) {
      toast.push("Não foi possível adicionar o módulo.");
      return;
    }
    setMods((s) => [...s, data as ModuleRow]);
    toast.push("Módulo criado.");
  }

  async function renameModule(m: ModuleRow) {
    const t = prompt("Novo título do módulo:", m.title);
    if (!t || t === m.title) return;
    const { data, error } = await sb
      .from("modules")
      .update({ title: t, updated_at: new Date().toISOString() })
      .eq("id", m.id)
      .select("*")
      .single();
    if (error) {
      toast.push("Falha ao renomear.");
      return;
    }
    setMods((s) => s.map((x) => (x.id === m.id ? (data as ModuleRow) : x)));
  }

  async function toggleModule(m: ModuleRow) {
    const { data, error } = await sb
      .from("modules")
      .update({ is_active: !m.is_active })
      .eq("id", m.id)
      .select("*")
      .single();
    if (error) {
      toast.push("Falha ao alterar módulo.");
      return;
    }
    setMods((s) => s.map((x) => (x.id === m.id ? (data as ModuleRow) : x)));
  }

  async function deleteModule(m: ModuleRow) {
    const ok = confirm(`Excluir módulo "${m.title}"?`);
    if (!ok) return;
    const { error } = await sb.from("modules").delete().eq("id", m.id);
    if (error) {
      toast.push("Falha ao excluir módulo.");
      return;
    }
    setMods((s) => s.filter((x) => x.id !== m.id));
    toast.push("Módulo excluído.");
  }

  async function moveModule(m: ModuleRow, dir: "up" | "down") {
    const idx = mods.findIndex((x) => x.id === m.id);
    if (idx < 0) return;

    const swapIdx = dir === "up" ? idx - 1 : idx + 1;
    if (swapIdx < 0 || swapIdx >= mods.length) return;

    const a = mods[idx];
    const b = mods[swapIdx];

    // troca local
    const newList = [...mods];
    newList[idx] = { ...b, sort_order: a.sort_order };
    newList[swapIdx] = { ...a, sort_order: b.sort_order };
    setMods(newList);

    // persiste
    await sb.from("modules").update({ sort_order: newList[idx].sort_order }).eq("id", newList[idx].id);
    await sb.from("modules").update({ sort_order: newList[swapIdx].sort_order }).eq("id", newList[swapIdx].id);
    toast.push("Ordem salva.");
  }

  return (
    <AnimatePresence>
      {open && (
        <motion.div
          key="drawer"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          className="fixed inset-0 z-50"
          aria-modal
          role="dialog"
        >
          {/* backdrop */}
          <div className="absolute inset-0 bg-black/60" onClick={onClose} />

          {/* pane */}
          <motion.div
            initial={{ x: "100%" }}
            animate={{ x: 0 }}
            exit={{ x: "100%" }}
            transition={{ type: "spring", stiffness: 160, damping: 22 }}
            className="absolute right-0 top-0 h-full w-full max-w-xl
                       bg-gradient-to-b from-neutral-900 to-black
                       border-l border-white/10 shadow-2xl"
          >
            <div className="p-4 border-b border-white/10 flex items-center justify-between">
              <div>
                <div className="text-lime-300 font-semibold">Editar curso</div>
                <div className="text-xs text-white/60">{courseId}</div>
              </div>
              <button
                onClick={onClose}
                className="h-9 w-9 grid place-items-center rounded-lg bg-white/5 border border-white/10 hover:bg-white/10"
              >
                <X size={16} />
              </button>
            </div>

            {/* tabs */}
            <div className="px-4 pt-3 flex items-center gap-2">
              <button
                onClick={() => setTab("general")}
                className={`h-9 px-3 rounded-lg text-sm border ${tab==="general"
                  ? "bg-lime-400/10 text-lime-300 border-lime-400/20"
                  : "bg-white/5 border-white/10 hover:bg-white/10"}`}
              >
                Geral
              </button>
              <button
                onClick={() => setTab("modules")}
                className={`h-9 px-3 rounded-lg text-sm border ${tab==="modules"
                  ? "bg-lime-400/10 text-lime-300 border-lime-400/20"
                  : "bg-white/5 border-white/10 hover:bg-white/10"}`}
              >
                Módulos
              </button>
            </div>

            <div className="p-4 overflow-y-auto h-[calc(100%-50px-45px)]">
              {tab === "general" ? (
                <div className="space-y-4">
                  {loading ? (
                    <div className="animate-pulse space-y-3">
                      <div className="h-4 w-48 bg-white/10 rounded" />
                      <div className="h-10 bg-white/10 rounded" />
                      <div className="h-4 w-36 bg-white/10 rounded" />
                      <div className="h-24 bg-white/10 rounded" />
                    </div>
                  ) : (
                    <>
                      <div>
                        <label className="block text-sm opacity-70 mb-1">Título</label>
                        <input
                          value={title}
                          onChange={(e) => setTitle(e.target.value)}
                          className="w-full h-10 rounded-xl bg-white/5 border border-white/10 outline-none
                                     focus:ring-2 focus:ring-lime-400/25 px-3"
                        />
                      </div>
                      <div>
                        <label className="block text-sm opacity-70 mb-1">Descrição</label>
                        <textarea
                          value={desc ?? ""}
                          onChange={(e) => setDesc(e.target.value)}
                          rows={4}
                          className="w-full rounded-xl bg-white/5 border border-white/10 outline-none
                                     focus:ring-2 focus:ring-lime-400/25 px-3 py-2 resize-y"
                        />
                      </div>

                      <div className="flex items-center gap-2">
                        <button
                          onClick={() => setActive((v) => !v)}
                          className="inline-flex items-center gap-2 text-sm rounded-lg border px-3 py-2
                                     bg-white/5 border-white/10 hover:bg-white/10"
                        >
                          {active ? <ToggleRight size={16} className="text-lime-300" /> : <ToggleLeft size={16} className="text-white/60" />}
                          {active ? "Ativo" : "Inativo"}
                        </button>
                      </div>

                      <div className="pt-2">
                        <button
                          onClick={saveGeneral}
                          disabled={saving}
                          className="h-10 px-4 rounded-xl bg-lime-400/10 text-lime-300 hover:bg-lime-400/20
                                     hover:shadow-[0_0_10px_rgba(163,230,53,0.25)] transition inline-flex items-center gap-2"
                        >
                          {saving ? <Loader2 size={16} className="animate-spin" /> : <Save size={16} />} Salvar
                        </button>
                      </div>
                    </>
                  )}
                </div>
              ) : (
                <div className="space-y-3">
                  <div className="flex items-center justify-between">
                    <div className="text-sm text-white/70">Gerencie os módulos deste curso.</div>
                    <button
                      onClick={addModule}
                      className="h-9 px-3 rounded-lg bg-lime-400/10 text-lime-300 hover:bg-lime-400/20 inline-flex items-center gap-2"
                    >
                      <Plus size={14} /> Adicionar módulo
                    </button>
                  </div>

                  <div className="rounded-2xl overflow-hidden bg-white/[0.03] border border-white/10">
                    <div className="hidden md:grid grid-cols-[1fr,120px,120px,120px] px-4 py-2 text-xs uppercase tracking-wide text-white/60 border-b border-white/10">
                      <div>Título</div>
                      <div>Status</div>
                      <div>Ordem</div>
                      <div>Ações</div>
                    </div>

                    {modsLoading ? (
                      <ul className="divide-y divide-white/10 animate-pulse">
                        {Array.from({ length: 4 }).map((_, i) => (
                          <li key={i} className="grid md:grid-cols-[1fr,120px,120px,120px] grid-cols-1 gap-3 px-4 py-3">
                            <div className="h-4 w-64 bg-white/10 rounded" />
                            <div className="h-4 w-16 bg-white/10 rounded" />
                            <div className="h-4 w-20 bg-white/10 rounded" />
                            <div className="h-8 w-24 bg-white/10 rounded" />
                          </li>
                        ))}
                      </ul>
                    ) : mods.length === 0 ? (
                      <div className="px-4 py-6 text-center text-white/70">Nenhum módulo.</div>
                    ) : (
                      <ul className="divide-y divide-white/10">
                        {mods.map((m, idx) => (
                          <li key={m.id} className="grid md:grid-cols-[1fr,120px,120px,120px] grid-cols-1 gap-3 px-4 py-3">
                            <div className="text-white/90">
                              <button
                                onClick={() => renameModule(m)}
                                className="hover:underline"
                                title="Renomear"
                              >
                                {m.title}
                              </button>
                            </div>
                            <div className="self-center">
                              <button
                                onClick={() => toggleModule(m)}
                                className="inline-flex items-center gap-2 text-[11px] font-semibold px-2 py-1 rounded-md border
                                           bg-white/5 border-white/10 hover:bg-white/10"
                              >
                                {m.is_active ? <Check size={14} className="text-lime-300" /> : <X size={14} className="text-white/60" />}
                                {m.is_active ? "Ativo" : "Inativo"}
                              </button>
                            </div>
                            <div className="self-center flex items-center gap-1">
                              <button
                                onClick={() => moveModule(m, "up")}
                                disabled={idx === 0}
                                className="h-8 w-8 grid place-items-center rounded-lg bg-white/5 border border-white/10 hover:bg-white/10 disabled:opacity-40"
                                title="Acima"
                              >
                                <ChevronUp size={16} />
                              </button>
                              <button
                                onClick={() => moveModule(m, "down")}
                                disabled={idx === mods.length - 1}
                                className="h-8 w-8 grid place-items-center rounded-lg bg-white/5 border border-white/10 hover:bg-white/10 disabled:opacity-40"
                                title="Abaixo"
                              >
                                <ChevronDown size={16} />
                              </button>
                            </div>
                            <div className="self-center">
                              <button
                                onClick={() => deleteModule(m)}
                                className="h-8 px-3 rounded-lg bg-red-500/15 border border-red-400/30 text-red-300 hover:bg-red-500/25 inline-flex items-center gap-2"
                              >
                                <Trash2 size={14} /> Excluir
                              </button>
                            </div>
                          </li>
                        ))}
                      </ul>
                    )}
                  </div>
                </div>
              )}
            </div>
          </motion.div>
        </motion.div>
      )}
    </AnimatePresence>
  );
}
